.custom-input-number {
  width: 100%;
}
.custom-input-number .number-input {
  width: 100%;
  height: 32px;
  padding: 0 10px;
  background: #1E293B;
  border: 1px solid #334155;
  border-radius: 6px;
  color: #ffffff;
  font-size: 12px;
  transition: all 0.2s ease;
  outline: none;
}
.custom-input-number .number-input::placeholder {
  color: #64748b;
}
.custom-input-number .number-input:hover:not(:disabled) {
  border-color: #10B981;
}
.custom-input-number .number-input:focus {
  border-color: #10B981;
  background: #0F172A;
  box-shadow: 0 0 0 3px rgba(16, 185, 129, 0.1);
}
.custom-input-number .number-input:disabled {
  opacity: 0.5;
  cursor: not-allowed;
  background: #0F172A;
}
.custom-input-number .number-input::-webkit-inner-spin-button, .custom-input-number .number-input::-webkit-outer-spin-button {
  -webkit-appearance: none;
  margin: 0;
}
.custom-input-number .number-input[type=number] {
  -moz-appearance: textfield;
}
.custom-input-number.error .number-input {
  border-color: #ef4444;
}
.custom-input-number.error .number-input:focus {
  border-color: #ef4444;
  box-shadow: 0 0 0 3px rgba(239, 68, 68, 0.1);
}
.custom-input-number.disabled {
  opacity: 0.5;
  cursor: not-allowed;
}

:host-context(body.light-theme) .custom-input-number .number-input {
  background: #ffffff;
  border-color: #e2e8f0;
  color: #0f172a;
}
:host-context(body.light-theme) .custom-input-number .number-input::placeholder {
  color: #94a3b8;
}
:host-context(body.light-theme) .custom-input-number .number-input:hover:not(:disabled) {
  border-color: #10B981;
}
:host-context(body.light-theme) .custom-input-number .number-input:focus {
  border-color: #10B981;
  background: #f8fafc;
}
:host-context(body.light-theme) .custom-input-number .number-input:disabled {
  background: #f8fafc;
}
